<template>
  <div class="UserBox"
       @mouseenter="showBtn"
       @mouseleave="showBtn"
  >
    <el-popover
        placement="bottom"
        trigger="hover"
        :width="170"
    >
      <template #reference>
        <div
            class="img"
            :style="{
            backgroundImage: `url(${baseURL}${userInfo.photo}${random})`
          }"
            :title="userInfo.user"

        ></div>
      </template>

    </el-popover>
    <div v-if="ifshow" class="showBtn">
      <el-button type="primary"  @click="info">修改信息</el-button>
      <el-button type="danger"  @click="logout">退出登录</el-button>
    </div>
  </div>
</template>

<script>
import {mapState, mapMutations} from "vuex"

export default {
  name: "UserBox",
  data() {
    return {
      random: `?_=${Date.now()}`,
      ifshow:false
    }
  },
  computed: {
    ...mapState(['userInfo'])
  },
  watch: {
    userInfo() {
      this.random = `?_=${Date.now()}`

    }
  },
  methods: {
    ...mapMutations(['loginOut']),
    //退出登录
    async logout() {
      let {data} = await this.$axios({
        method: "POST",
        url: "/login/out"
      })

      if (data.code) return

      //退出登录成功,清空vuex
      this.$message.success("已退出登录")
      this.loginOut()
    },
    //修改个人信息
    info() {
      this.$router.push("/user")
    },
    showBtn(){
      this.ifshow=!this.ifshow
    }
  }
}
</script>

<style  lang="less">
.UserBox {
  width: 55px;
  height: 55px;
  padding: 8px 0;
  position: relative;

  .img {
    width: 55px;
    height: 55px;
    background: none center center/cover;
    border-radius: 50%;
    cursor: pointer;
  }
  .showBtn{
    width: 200px;
    height: 50px;
    background: #ffff;
    display: flex;
    justify-content: center;
    border-radius:5px ;
    align-items: center;
    position: absolute;
    right: 10px;
  }
}
</style>














