<template>
  <div id="app">
    <!-- 准备一个容器用来存放音乐播放器 -->
    <div id="player"></div>
  </div>
</template>

<script itemscope>
import APlayer from 'aplayer';
import 'aplayer/dist/APlayer.min.css';
export default {
  name: "App",
  data() {
    return {
      music: [ // 歌曲列表
        {
          artist: "",
          cover: "",
          lrc: "",
          name: "",
          theme: "",
          url: "",

        },
        ],
    }
  },
  methods: {

    initAudio(e) {
      // 创建一个音乐播放器实例，并挂载到DOM上，同时进行相关配置
      const ap = new APlayer({
        fixed: true,
        container: document.getElementById('player'),
        listFolded: false,
        listMaxHeight: "160px",
        lrcType: 3,
        audio: this.music
      });
    },

   async getMic(){

      let{data}=await  this.$axios({
          method:"GET",
          url:"/get/music"
        })
     if(data.code)return
     this.music=data.data
    }
  },
  async created() {
    await this.getMic()
    this.initAudio();
  },
  mounted() {
    // 初始化播放器


  }

}
</script>

<style lang="less" scoped>
#app {

  z-index: 9999;
  :deep(.aplayer .aplayer-list ol){
    &::-webkit-scrollbar { // 隐藏滚动条
      display: none;
    }
  }
  :deep(.aplayer.aplayer-fixed .aplayer-lrc){
    display: block;
    position: fixed;
     bottom: 30%;

    left: -80%;
    right: 0;
    margin: 0;
    z-index: 9999 !important;
    pointer-events: none;
    text-shadow: -1px -1px 0 #fff;
  }
  :deep(.aplayer .aplayer-lrc p.aplayer-lrc-current ){
    /* 大小 */
    font-size: 16px;
    /* 颜色 */
    color:  deeppink;
    opacity: 1;
    overflow: visible;
    height: auto !important;
    min-height: 16px;
  }
  :deep(.aplayer .aplayer-lrc p) {
    font-weight: 700;
    font-size:13px;
    color:#8967FC;
    line-height:16px!important;
    height:16px!important;
    padding:0!important;
    margin:0 !important;
    transition:all .5s ease-out;
    opacity:.4;
    overflow:hidden
  }

  #player {
    width: 310px;
    // 定个宽度
  }
}
</style>
