    </div>

  </div>

</template>

<script>

export default {
  data() {
    return {
      getMusic:[],
      carouselInfo: [
        {}
      ],
      // 当前索引
      currentIndex: 0,
      // 定时器
      timer: '',
      // 小圆点开关
      begin: '',
    }
  },
  methods: {
  //获取轮播图
    async getSwiper(){
      let {data}=await this.$axios({
        method:"GET",
        url:"/get/swiper"
      })
      if(data.code){
        return this.$message.error(data.msg)
      }
      this.carouselInfo=data.data

    },
    go() {
      this.timer = setInterval(() => {
        this.autoPlay()
      }, 3000)
    },
    stop() {
      clearInterval(this.timer);
      this.timer = null
    },
    change(index) {
      this.currentIndex = index
    },
    autoPlay() {
      this.currentIndex++;
      if (this.currentIndex > this.carouselInfo.length - 1) {
        this.currentIndex = 0
      }

    },
    carouseTransition() {
      this.begin = false;
    },
  },
  mounted(){

    // this.$nextTick(() => {
    //   if (this.begin) {
    //     return;
    //   }
    //   this.begin = true;
    //   this.timer = setInterval(() => {
    //     this.autoPlay()
    //   }, 3000)
    // });
    clearInterval(this.timer)
   this.go()
    this.getSwiper()

  },
}


</script>



<style scoped lang="less">
.hj-carousel {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-size: cover;
  margin: 0;
  .list-item {
    position: absolute;
    width:100%;
    height:100%;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-position: center center;
    background-size: cover;
    .intro-box {
      position: relative;
      height: 100%;
      .service-intro {
        width:418px;
        position: absolute;
        top: 152px;
        left: 58px;
        color: #FFF;
        h2 {
          height:53px;
          line-height:53px;
          font-size:38px;
          font-weight:500;
        }
        p {
          height: 34px;
          line-height: 34px;
          font-size:18px;
        }
        .intro-btn {
          display: block;
          margin-top: 33px;
          width:123px;
          height:40px;
          line-height: 40px;
          text-align: center;
          background:linear-gradient(360deg,rgba(228,39,53,1) 0%,rgba(235,121,101,1) 100%);
          box-shadow:0 5px 20px 0 rgba(230,2,19,0.5);
          border-radius:5px;
          color: #FFF;
          text-decoration: none;
        }
      }
    }
  }
  .circles-items {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 16px;
    z-index: 10;
    margin: 0 auto;
    text-align: center;
    font-size: 0;
    overflow: hidden;
  }
  .swiperBtn{
    position: absolute;
    right: 15%;
    bottom: 15%;
    z-index: 996;
    cursor: pointer;
    &:hover{
     transform: rotateZ(10deg);
    }
  }

}



.list-enter,
.list-leave-to {
  width: 0;
  height: 0;
  opacity: 0;
}

.list-leave-active,
.list-enter-active {
  transition: all 2s linear;
}


</style>




